<template>
  <div class="message">
    <Panel class="old">
      <template #header>
        <router-link to="/" class="gohome">首页</router-link>
        <span class="xx">/</span>
        <span class="sconent">新消息</span>
      </template>
      <template #content v-if="hasnot_read_messages.length">
        <MessageItem
          v-for="item in hasnot_read_messages"
          :key="item.id"
          :item="item"
        />
      </template>
      <template #content v-else>
        <span>暂无消息</span>
      </template>
    </Panel>
    <Panel class="old">
      <template #header>
        <span class="gohome">过往消息</span>
      </template>
      <template #content v-if="has_read_messages.length">
        <MessageItem
          v-for="item in has_read_messages"
          :key="item.id"
          :item="item"
        />
      </template>
      <template #content v-else>
        <span>暂无消息</span>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import { mapActions, mapState } from "vuex";
import MessageItem from "../components/MessageItem.vue";
export default {
  components: { Panel, MessageItem },
  created() {
    this.getMessage();
    this.changeRead();
  },
  computed: {
    ...mapState({
      has_read_messages: (state) => state.message.has_read_messages,
      hasnot_read_messages: (state) => state.message.hasnot_read_messages,
    }),
  },
  methods: {
    ...mapActions(["getMessage", "changeRead"]),
  },
};
</script>

<style lang="less">
.message {
  .gohome {
    color: #fff;
  }
  .xx {
    color: #999999;
    margin: 0 5px;
  }
  .sconent {
    color: #999999;
  }
  .old {
    margin-top: 20px;
  }
}
</style>
